.loader {
    width: 40px;
    height: 40px;
    border: 4px solid transparent;
    border-radius: 50%;
    border-top-color: #3498db;
    border-left-color: #3498db;
    animation: load 0.9s linear infinite;
    margin: 20px auto;
}
